<template>
	<div
		id="weather-app"
		:class="
			typeof weather.main != 'undefined' && weather.main.temp > 16 ? 'warm' : ''
		"
	>
		<main>
			<div class="container">
				<div class="search-box">
					<input
						type="text"
						class="search-bar"
						placeholder="Enter Your Location..."
						v-model="query"
						@keypress="getWeather"
					/>
				</div>
				<div class="weather-info" v-if="typeof weather.main != 'undefined'">
					<div class="location-box">
						<div class="location">
							{{ weather.name }}, {{ weather.sys.country }}
						</div>
						<div class="date">{{ setdate() }}</div>
					</div>
					<div class="weather-box">
						<div class="temp">{{ Math.round(weather.main.temp) }} Â°C</div>
						<div class="weather">{{ weather.weather[0].main }}</div>
					</div>
				</div>
			</div>
		</main>
	</div>
</template>

<script>
export default {
	name: "App",
	data() {
		return {
			api_key: "c39c8bc88991394ba45edcf2b0a47559",
			url_base: "https://api.openweathermap.org/data/2.5/",
			query: "",
			weather: {},
		};
	},
	methods: {
		getWeather(e) {
			if (e.key == "Enter") {
				fetch(
					`${this.url_base}weather?q=${this.query}&units=metric&APPID=${this.api_key}`
				)
					.then((res) => {
						return res.json();
					})
					.then(this.setResults);
			}
		},
		setResults(results) {
			return (this.weather = results);
		},
		setdate() {
			let x = new Date();
			let mounths = [
				"Jan",
				"Feb",
				"Mar",
				"Apr",
				"May",
				"Jun",
				"Jul",
				"Aug",
				"Sep",
				"Oct",
				"Nov",
				"Dec",
			];
			let days = [
				"Monday",
				"Tuesday",
				"Wednesday",
				"Thursday",
				"Friday",
				"Saturday",
				"Sunday",
			];
			let day = days[x.getDay()];
			let date = x.getDate();
			let mounth = mounths[x.getMonth()];
			let year = x.getFullYear();
			return `${day} ${date} ${mounth} ${year}`;
		},
	},
};
</script>

